import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import Select from "react-select";
import makeAnimated from 'react-select/animated';

import slide1 from "../../images/post-1.jpg";
import slide2 from "../../images/post-2.jpg";
import slide3 from "../../images/post-3.jpg";
import { SignalCellularNullTwoTone } from "@material-ui/icons";

const animatedComponents = makeAnimated();

function SliderOverlay (props) {
    const dropList = [
        {value: "abia", label: "Abia"},
        {value: "adamawa", label: "Adamawa"},
        {value: "akwa ibom", label: "Akwa ibon"},
        {value: "anambra", label: "Ananmbra"},
        {value: "bauchi", label: "Bauchi"},
        {value: "bayelsa", label: "Bayelsa"},
        {value: "benue", label: "Benue"},
        {value: "borno", label: "Borno"},
        {value: "cross river", label: "Cross River"},
        {value: "delta", label: "Delta"},
        {value: "edo", label: "Edo"},
        {value: "enugu", label: "Enugu"},
        {value: "gombe", label: "Gombe"},
        {value: "jigawa", label: "Jigawa"},
        {value: "kaduna", label: "Kaduna"},
        {value: "kwara", label: "Kwara"}
    ];

    const [ selectedValue, setSelectedValue ] = React.useState(null);

    const handleChange = (obj) => {
        setSelectedValue(obj);
    }

    return (
        <div className={props.class}>
            <Select 
                components={animatedComponents}
                closeMenuOnSelect={false}
                isMulti
                value={selectedValue}
                options={dropList} 
                onChange={handleChange}
            />
            <br />
            <div>
                <b>Selected Value: </b>
                <pre>{JSON.stringify(selectedValue)}</pre>
            </div>

        </div>
    )
}

function Slide (props) {
    return (
        <div className={props.class} style={{backgroundImage: props.src}}></div>
    )
}

function BannerSlider (props){

    const state = [
        {image: slide1},
        {image: slide2},
        {image: slide3}
    ]

    const settings = {
        dots: false,
        infinite: true,
        speed: 1000,
        slidesToShow: 1,
        slidesToScroll: 1,
        autoplay: true,
        autoplaySpeed: 4000
    };

    const ShowSlides = () => {
        const slide = state.map(item => {
            return <Slide class="slide" src={`url(${item.image})`} />;
        })
        return slide;
    }

    return (
        <div className="banner-area">
            <SliderOverlay />
            <Slider {...settings}>
                {ShowSlides()}
            </Slider>
        </div>
    )
    
}


export default BannerSlider;